/* STRUCTURAL */
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}
.navbar {
  background-color: #392c44;
}
h1 {
  font-size: 1.5rem;
  padding: 20px 0px 10px 0px;
}
a {
  color: #3598ba;
}
a:hover {
  text-decoration: none;
  color: #6b567c;
}

/* TABLE COMPONENT */
caption {
  padding: 0.3rem;
}
tbody tr:hover {
  color: #6b567c;
}
.table td,
.table th {
  border: none;
}
.pagination {
  display: block;
  margin-top: 20px;
}
.page-scroll {
  overflow-x: auto;
}
#page-numbers {
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  margin-top: -15px;
  padding-bottom: 5px;
  margin-bottom: 10px;
}
#page-numbers > li {
  font-size: 1.2rem;
  padding-left: 8px;
  padding-right: 8px;
  color: #3598ba;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
#page-numbers > li:hover {
  background-color: #dddddd;
  color: #6b567c;
}

/* MAP COMPONENT */
.mapboxgl-map {
  margin-bottom: 20px;
  margin-top: 10px;
}
.overlays:active {
  opacity: 0.6;
}
.overlays canvas {
  mix-blend-mode: multiply;
}
.legend {
  margin-bottom: -5px;
  margin-top: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#comm {
  color: rgb(191, 51, 179);
}
#sing {
  color: rgb(60, 152, 209);
}
#mult {
  color: rgb(255, 206, 102);
}
#indu {
  color: rgb(255, 51, 102);
}
#inst {
  color: rgb(31, 224, 115);
}
#unknown {
  color: rgb(86, 86, 86);
}

/*MEDIA QUERIES*/
@media (min-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }
  .mapboxgl-map {
    margin-bottom: 50px;
  }
}



